.panel-page {
  min-width: 1000px;
  min-height: 700px;
  background: #121b73 url(./static/media/bg@2x.eb6c4b6f.png) no-repeat center;
  background-size: cover;
  color: #fff;
}
.panel-page .header {
  background: linear-gradient(to right, #0F48A8, #08227A);
  position: relative;
  text-align: center;
  color: #fff;
}
.panel-page .header .time {
  position: absolute;
  font-size: 16px;
  left: 50px;
}
.panel-page .header .title {
  font-size: 30px;
  font-weight: bolder;
  width: 421px;
  margin: 0 auto;
  background: url(./static/media/biaoti@2x.93b5c187.png) no-repeat center;
  background-size: contain;
}
.panel-page .content {
  padding: 0 30px;
}
.panel-page .content .chartWrap {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.4);
  margin-top: 30px;
  position: relative;
}
.panel-page .content .chartWrap .section-head {
  position: absolute;
  top: -15px;
  left: 50%;
  height: 30px;
  line-height: 30px;
  margin-left: -75px;
  width: 150px;
  color: #fff;
  background-color: blue;
  font-size: 14px;
  text-align: center;
  font-weight: bold;
}
.panel-page .content .center {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 50vh;
  padding: 30px 0;
  min-height: 300px;
  background-image: url(./static/media/gailan@2x.d4214e2d.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 200px;
}
.panel-page .content .center Row {
  flex: 1 1;
}
.panel-page .content .center-num {
  text-align: center;
  border-radius: 4px;
  border: 1px solid rgba(56, 192, 255, 0.7);
  background: rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 3px rgba(255, 255, 255, 0.2);
  padding: 10px;
}
.panel-page .content .center-num .num-text {
  font-size: 20px;
  color: #38C0FF;
  font-weight: 900;
  font-family: Arial, Helvetica, sans-serif;
}
.panel-page .content .center-num .num-label {
  font-size: 12px;
  color: #fff;
}
.panel-page .footer {
  background: none;
  text-align: center;
  color: #fff;
}
